
.block{
    filter: contrast(10);
}
h1{
    width: 100%;
    text-align: center;
    position: absolute;
    font-size: 120px;
    margin: 0;
    color: #000000;
    text-shadow:
            4px  4px 120px rgba(256, 256, 256, 1),
            -4px -4px 120px rgba(256, 256, 256, 1),
            -4px  4px 120px rgba(256, 256, 256, 1),
            4px -4px 120px rgba(256, 256, 256, 1)
;

}

@keyframes anim {
    0%   { opacity: 0; filter: blur(28px)}
    10%  { opacity: 0; }
    90%  { opacity: 1; }
    100% { opacity: 1; filter: blur(6px)}
}
.A{
    animation: anim 4s infinite alternate-reverse;
}
.B{
    animation: anim 4s infinite alternate;
}